import React, {useContext, useState} from 'react';
import Images from '../share/Img';
import { Link, useHistory } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './register.css';

export default function Register() {
    const {RegisterUser} = useContext(AuthContext);

    const [RegisterForm, setRegisterForm] = useState({
        email: '',
        check_email:'',
        password: '',
        fullName: ''
    })

    const {email, check_email, password, fullName} = RegisterForm;


    const onChangeRegisterForm = event => 
        setRegisterForm({
            ...RegisterForm, [event.target.name]: event.target.value
        })

        function validateEmail() {
            const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(email);
        }

        let matches = fullName.match(/\d+/g);


    const register = async event => {
        event.preventDefault()

        if(!validateEmail(email)) {
            toast.error("Unknown email address");
            return;
        }

        if (password.length < 6) {
            toast.error("Password is too short");
            return;
        }

        if (email !== check_email) {
            toast.warning("Confirm email does not match");
            return;
        }

        if (fullName.length < 2) {
            toast.warning("You should use your real name");
            return;
        }

        if (matches != null) {
            toast.warning("Your name should not be a number");
        }
    
            try{
                const registerData = await RegisterUser(RegisterForm)
                if(!registerData){
                    toast.error("Email already exists !")
                }
            } catch (error) {
                toast.error(error.message)
            }   
        }

    return (
        <div className="register">
            <div className="header">
                <img 
                    src ={Images.LOGO_BLACK}
                    alt = ""
                />
            </div>
            <div className="container">
                <h1 className="t-center">ng k min ph  bt u nghe.</h1>
                <button className="t-center btn_fbm">ng k bng Facebook</button>
                <div className="divider">
                    <strong>HOC</strong>
                </div>
                <form onSubmit={register}>
                    <div className="content">
                        <div className="form">
                            <label>Email ca bn l g ?</label>
                            <input type="text" name="email" required value={email} onChange={onChangeRegisterForm} placeholder="Nhp email ca bn." className="form_input"/>
                            <p className= "p_phone">Dng s in thoi.</p>
                        </div>
                        <div className="form">
                            <label>Xc nhn email ca bn</label>
                            <input type="text" name="check_email" value={check_email} onChange={onChangeRegisterForm} placeholder="Nhp li email ca bn." className="form_input"/>
                        </div>
                        <div className="form">
                            <label>To mt khu</label>
                            <input type="password" name="password" required value={password} onChange={onChangeRegisterForm} placeholder="To mt khu." className="form_input"/>
                        </div>
                        <div className="form">
                            <label>Bn tn l g</label>
                            <input type="text" name="fullName" required value={fullName} onChange={onChangeRegisterForm} placeholder="Nhp tn h s." className="form_input"/>
                            <p>Tn ny s xut hin trong h s ca bn .</p>
                        </div>
                    <div className = "check">
                        <h4>Bn sinh ngy no ?</h4>
                            <input name="dob" type="date" id="birthday" name="birthday"/>
                        <h4>Gii tnh ca bn l g</h4>
                        <div className="box">
                                <label className="check_box">Nam
                                    <input type="radio" checked="checked" name="radio"/>
                                    <span className="checkmark"></span>
                                </label>
                                <label className="check_box">N
                                    <input type="radio" checked="checked" name="radio"/>
                                    <span className="checkmark"></span>
                                </label>
                                <label className="check_box">Khng phn bit gii tnh
                                    <input type="radio" checked="checked" name="radio"/>
                                    <span className="checkmark"></span>
                                </label>
                        </div>
                        <div className="check_box1">
                            <label>
                                <input type="checkbox" checked="checked"/>
                                Ti khng mun nhn tin nhn tip th t Spotify
                            </label>
                            <label>
                                <input type="checkbox"/>
                                Chia s d liu ng k ca ti vi cc nh cung cp ni dung ca Spotify cho mc ch tip th.
                            </label>
                        </div>
                    </div>
                    </div>
                    <button type="submit" className="btn_sgnu">ng K</button>
                    <ToastContainer />
                   </form>
                   <span>
                       Bn  c ti khon?
                       <Link to='/login'><a> ng nhp</a></Link>
                   </span>
            </div>
        </div>
    )
}
